.MyCustomElement {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#WACContainer.WACContainer .HideWebChat {
  display: none;
}
#WACContainer.WACContainer .WACBackgroundCover {
  border-radius: 0 !important;
}
#WACContainer.WACContainer .WAC--aiTheme .WACWidget.WACWidget {
  background:none !important;
  border: none !important;
}

// chat header
#WACContainer.WACContainer .WACHeader {
  border-radius: 0 !important;
}

// chat header icons
#WACContainer.WACContainer .WACHeader__BackButton,
#WACContainer.WACContainer .WACHeader__RestartButton,
#WACContainer.WACContainer .WACHeader__OverflowMenu {
  border-radius: 0 !important;
}
#WACContainer.WACContainer .WACHeader__LeftButtons span.cds--popover--right span.cds--popover span.cds--popover-content {
  padding: 12px;
}
