#WACContainer.WACContainer .WACHeader__RightButtons span.cds--popover--top span.cds--popover {
  position: relative;
  inset: 70px -50px;
}
#WACContainer.WACContainer .WACHeader__RightButtons span.cds--popover--top span.cds--popover span.cds--popover-content {
  padding: 12px;
}
#WACContainer.WACContainer .WACHeader__RightButtons span.cds--popover--top span.cds--popover span.cds--popover-caret {
  clip-path: polygon(0% 100%, 50% 0%, 100% 100%);
  inset-block-start: -335%;
  transform: translate(750%, calc(var(--cds-popover-offset)*1));
}

// suggestion icon 
