// }

// chat background
#WACContainer.WACContainer .WAC__messagesAndInputContainer,
#WACContainer.WACContainer .WACHomeScreen__contentWrapper {
  background: linear-gradient(to bottom, var(--cds-layer-02) 50%, rgba(69, 137, 255, 0.2)) !important;
}
#WACContainer.WACContainer .WAC--aiTheme .WAC__messages {
  background-image: none !important;
  box-shadow: none !important;
}
#WACContainer.WACContainer .WAC--aiTheme .WAC__messages .WAC__received--inner main.WxA__main-content-wrapper {
  background: none !important;
}

// chat input
#WACContainer.WACContainer .WAC__overlay-panel .WACInputContainer {
  border-radius: 8px !important;
  margin: 16px !important;
}
#WACContainer.WACContainer .WAC__overlay-panel .WACInputContainer span.cds--popover--top-right span.cds--popover span.cds--popover-content {
  padding: 12px;
}
#WACContainer.WACContainer .WACBotContainer .WACInputContainer {
  border-radius: 8px !important;
  margin: 16px !important;
  width: auto !important;
}
#WACContainer.WACContainer .WACBotContainer .WACInputContainer span.cds--popover--top-right span.cds--popover span.cds--popover-content {
  padding: 12px;
}